
* {
  box-sizing: border-box;
  transition: all 0.3s ease;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to

/* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
html,
body {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}
#app {
  margin: 0 auto;
  width: 100%;
  height: 100%;
  /* background: linear-gradient(to right bottom, rgba(255, 210, 111, 0.6), rgba(54, 119, 255, 0.6)); */
}
.home_video {
  height: 50%;
  width: 50%;
}
.home_audio_inint {
  cursor: pointer;
  color: #ffffff;
  top: 0%;
  right: 0%;
  z-index: 999;
  position: fixed;
  height: 100%;
  width: 100%;
  font-size: 18px;
  font-weight: bolder;
  background-color: rgba(35, 10, 8, 0.6);
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: initial;
}
.home_vlog {
  cursor: pointer;
  color: #ffffff;
  top: 15%;
  right: 6%;
  z-index: 999;
  position: absolute;
  height: 30px;
  width: 30px;
  font-size: 18px;
  font-weight: bolder;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: initial;
  animation: audioVlog 2s infinite alternate;
}
.home_audio {
  cursor: pointer;
  color: #ffffff;
  top: 5%;
  right: 6%;
  z-index: 999;
  position: absolute;
  height: 30px;
  width: 30px;
  font-size: 18px;
  font-weight: bolder;
  background-color: rgba(35, 10, 8, 1);
  border-radius: 50%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: initial;
  animation: audioIn 3s infinite;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
}
.home_audio_audio {
  display: none;
}
.home_audio_div {
  box-sizing: border-box;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  height: 30px;
  width: 30px;
  line-height: 30px;
  vertical-align: top;
  border-radius: 50%;
}
.home_egg_particles {
  width: 100%;
  height: 100%;
  z-index: 0;
}
.home_egg::-webkit-scrollbar {
  /* height: 10px; */
  width: 8px;
}

/* 滚动条的滑轨背景颜色 */
.home_egg::-webkit-scrollbar-track {
  background-color: #fdfdfd;
}

/* 滑块颜色 */
.home_egg::-webkit-scrollbar-thumb {
  background-color: rgba(63, 63, 63, 0.2);
  border-radius: 5px;
}

/* 滑轨两头的监听按钮颜色 */
.home_egg::-webkit-scrollbar-button {
  height: 10px;
  background-color: #fdfdfd;
}
.home_top_muen {
  cursor: pointer;
  color: #000000;
  top: 0px;
  z-index: 999;
  position: absolute;
  text-align: center;
  left: 0rem;
  height: 50px;
  width: 50px;
  line-height: 50px;
  font-size: 2em;
  color: #999999;
  border-radius: 50%;
  display: none;
}
@media (max-width: 1400px) {
.home {
    background-color: #fdfdfd;
}
.home_egg_botom_button {
    border: 2px solid #b6c1c7;
}
}
@media screen and (min-width: 35.5rem) and (max-width: 55.25rem) {
