.fensug {
  width: 9.25rem;
  height: 9.25rem;
  background: #fff;
  border: 9px #5576ac solid;
  margin: 100px auto;
  padding: 5px;
  -webkit-border-radius: 25em;
  -moz-border-radius: 25em;
  border-radius: 25em;
  overflow: hidden;
  position: relative;
}
.fensug .wavenum {
  width: 9.25rem;
  height: 9.25rem;
  overflow: hidden;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
  position: absolute;
  left: -10px;
  top: -5px;
  z-index: 6;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.fensug .wavenum b {
  color: #fff;
  font-size: 30px;
  text-align: center;
  display: block;
  position: relative;
  z-index: 2;
  line-height: 45px;
}
.fensug .wavenum tt {
  color: #fff;
  font-size: 16px;
  text-align: center;
  display: block;
  position: relative;
  z-index: 2;
  font-weight: bold;
  width: 124px;
  border-top: 2px #fff solid;
  margin: 3px auto;
  line-height: 35px;
}
.waven {
  /* width: 168px; */
  height: 7.5rem;
  border-radius: 25em;
  background-color: #5576ac;
  overflow: hidden;
  position: relative;
  z-index: 5;
}
.wave {
  width: 25.5rem;
  height: 80%;
  position: absolute;
  left: 0px;
  bottom: 0px;
  z-index: 4;
  background: url(/space/dist/static/img/wave.739f04e.png) no-repeat;
  animation: move_wave 1s linear infinite;
  -webkit-animation: move_wave 1s linear infinite;
}
@keyframes move_wave {
0% {
    transform: translateX(0);
}
50% {
    transform: translateX(-25%);
}
100% {
    transform: translateX(-50%);
}
}

